<template>
  <div>
    <div class="top"></div>
    <div class="tle">硅谷外卖</div>
    <van-tabs>
      <van-tab title="登录">
        <van-form @submit="logOn">
          <van-field
            v-model="uname"
            name="用户名"
            placeholder="用户名"
            :rules="[{ required: true, message: '请填写用户名' }]"
          />
          <van-field
            v-model="upd"
            type="password"
            name="密码"
            placeholder="密码"
            :rules="[{ required: true, message: '请填写密码' }]"
          />
          <van-field
            v-model="sms"
            center
            clearable
            label="验证码"
            placeholder="请输入验证码"
            maxlength="6"
            :rules="[
              { required: true, message: '请填写验证码' },
              { validator: Verification, message: '验证码错误' },
            ]"
          >
            <template #button>
              <!-- <van-image width="100" height="50" :src="isrc" /> -->
              <div @click="refreshCode">
                <s-identify :identifyCode="identifyCode"></s-identify>
              </div>
            </template>
          </van-field>
          <div style="margin: 16px">
            <van-button round block type="primary" native-type="submit"
              >登录</van-button
            >
          </div>
        </van-form>
      </van-tab>
      <van-tab title="注册">
        <van-form @submit="signIn">
          <van-field
            v-model="uname"
            name="用户名"
            placeholder="用户名"
            :rules="[
              { required: true, message: '请填写用户名' },
              { validator: checkUname, message: '用户名已存在' },
            ]"
          />
          <van-field
            v-model="upd_"
            type="password"
            name="密码"
            placeholder="密码"
            :rules="[{ required: true, message: '请填写密码' }]"
          />
          <van-field
            v-model="reupd_"
            type="password"
            name="重复密码"
            placeholder="重复密码"
            :rules="[
              { required: true, message: '请重复填写密码' },
              { validator: checkPwd, message: '两次输入的密码不一致' },
            ]"
          />
          <van-field
            v-model="uname_"
            type="text"
            name="昵称"
            placeholder="昵称"
            :rules="[{ required: true, message: '请填写昵称' }]"
          />
          <div style="margin: 16px">
            <van-button round block type="primary" native-type="submit"
              >注册</van-button
            >
          </div>
        </van-form>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import Vue from "vue";
import SIdentify from "../components/identify.vue";
import { Tab, Tabs } from "vant";
import { Form } from "vant";
import { Field } from "vant";
import { Button } from "vant";
import { Notify } from "vant";
import { Image as VanImage } from "vant";

Vue.use(VanImage);
Vue.use(Button);
Vue.use(Field);
Vue.use(Form);
Vue.use(Tab);
Vue.use(Tabs);
export default {
  components: {
    SIdentify,
  },
  data() {
    return {
      //登录
      uname: "",//用户名& 注册
      upd: "",//密码
      sms: "",//验证码表单
      identifyCode: "", //验证码后端
      //注册
      upd_: "",//密码
      reupd_: "",//重复密码
      uname_: "",//姓名
      
      //用户信息
      udata:[],
    };
  },
  mounted() {
    this.identifyCode = "";
    this.getCode();
  },
  methods: {
    //--------------登录----------------

    //-验证码组件
    //刷新验证码
    refreshCode() {
      this.identifyCode = "";
      this.getCode();
    },
    //后端获取验证码
    getCode() {
      let app = this;
      app.$axios.get("/user/sendCaptcha").then(function (res) {
        // let code = res.data;
        // console.log(code);
        app.identifyCode = String(res.data);
        // app.sms = String(res.data);//-------------------------------------自动填写----
      });
    },
    //验证码验证
    Verification(val) {
      if (val != this.identifyCode) {
        return false;
      }
    },
    //登录提交
    logOn() {
      let app = this;
      app.$axios
        .post("/user/login", {
          account: app.uname,
          password: app.upd,
        })
        .then(function (res) {
          if(res.data==''){
          Notify({ type: "danger", message: "用户名或密码错误" });
          }else{
          Notify({ type: "success", message: "登录成功" });
          app.udata=res.data;
          app.$router.push({ name: 'indexstore', params: {uId: res.data.id, uName: res.data.name}})
          }
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    //--------------注册----------------
    //用户名存在验证
    checkUname(val) {
      let app = this;
      return app.$axios
        .post("/user/register", { account: val })
        .then(function (res) {
          return false;
        })
        .catch(function (error) {
          return true;
        });
    },
    //重复密码验证
    checkPwd() {
      if (this.upd_ != this.reupd_) {
        return false;
      }
    },
    //注册提交
    signIn() {
      let app = this;
      app.$axios
        .post("/user/register", {
          account: app.uname,
          password: app.upd_,
          name: app.uname_,
        })
        .then(function (res) {
          console.log(res.data);
          Notify({ type: "success", message: "注册成功" });
        })
        .catch(function (error) {
          console.log(error);
        });
    },
    //--------------------------------
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.top {
  width: 100%;
  height: 50px;
}
.tle {
  color: rgb(74, 164, 120);
  font-size: 45px;
  font-weight: bold;
  margin-bottom: 50px;
}
</style>



