import Vue from 'vue'
import Router from 'vue-router'
import Form from '../components/Form.vue'
import Index from '../components/Index.vue'
import Store from '../components/Store.vue'
import IndexStore from '../components/IndexStore.vue'
import IndexSearch from '../components/IndexSearch.vue'
import IndexOrder from '../components/IndexOrder.vue'
import IndexMine from '../components/IndexMine.vue'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/', component: Form },
    {
      path: '/index', component: Index, name: "index", props: true,
      children: [
        {
          path: 'indexstore',
          component: IndexStore,
          name: "indexstore",
          props: true,
        },
        {
          path: 'indexsearch',
          component: IndexSearch,
          name: "indexsearch",
          props: true,
        },
        {
          path: 'indexorder',
          component: IndexOrder,
          name: "indexorder",
          props: true,
        },
        {
          path: 'indexmine',
          component: IndexMine,
          name: "indexmine",
          props: true,
        },
      ]
    },
    {
      path: '/store', component: Store, name: "store", props: true
    },
  ],
})



