<template>
  <div>
    <van-nav-bar title="硅谷外卖"/>
    <router-view></router-view>

    <van-tabbar v-model="active" route style="border-top:1px solid #B3B3B4">
      <van-tabbar-item
        icon="wap-home-o"
        :to="{
          name: 'indexstore',
          params: { uId: uId, uName: uName },
        }"
        >商家</van-tabbar-item
      >
      <van-tabbar-item
        icon="search"
        :to="{
          name: 'indexsearch',
          params: { uId: uId, uName: uName },
        }"
        >搜索</van-tabbar-item
      >
      <van-tabbar-item
        icon="orders-o"
        :to="{
          name: 'indexorder',
          params: { uId: uId, uName: uName },
        }"
        >订单</van-tabbar-item
      >
      <van-tabbar-item icon="user-circle-o"
        :to="{
          name: 'indexmine',
          params: { uId: uId, uName: uName },
        }"
      >我的</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
import Vue from "vue";
import { NavBar } from "vant"; //
// import { Popup } from "vant";//
import { Icon } from "vant"; //
import { Tabbar, TabbarItem } from "vant";

Vue.use(Tabbar);
Vue.use(TabbarItem);
Vue.use(Icon);
// Vue.use(Popup);
Vue.use(NavBar);
export default {
  name: "index",
  props: ["uId", "uName"],
  data() {
    return {
      active: 0,
      // show1: false, //右侧弹出
      // show2: false, //左侧搜索
      // jiugongge: [], //九宫格
      // shangjia: [], //商家
    };
  },
  mounted() {},
  methods: {
  },
};
</script>



