<template>
  <div>
    <div class="head">外卖订单</div>
    <van-empty v-if="order==''" />
    <div class="order" v-else>
      <div v-for="(o, index) in order" :key="index">
        <div class="o">
          <div class="o_top">
            <div class="o_img">
              <van-image
                width="40"
                height="40"
                radius="5"
                :src="o.list[0].foodPhoto"
                ><template v-slot:loading>
                  <van-loading type="spinner" size="20" /> </template
              ></van-image>
            </div>
            <div class="o_name">
              <!-- <div class="o_name_n"> -->
              <router-link
                class="o_name_n"
                tag="div"
                :to="{
                  name: 'store',
                  params: { storeId: o.shopId, uId: uId, uName: uName },
                }"
              >
                <div class="o_name_n_left">{{ o.shopName }}</div>
                <div class="o_name_n_right">
                  <van-icon name="arrow" color="#7F7F7F" />
                </div>
              </router-link>

              <div class="o_name_t">{{ o.roderTime }}</div>
            </div>
            <div class="o_songda">已送达</div>
          </div>
          <div class="o_center">
            <div class="o_c_left">
              <div class="o_c_l_left">{{ o.list[0].foodName }}</div>
              <div class="o_c_r_right">等{{ o.list.length }}件商品</div>
            </div>
            <div class="o_c_right">￥68.86</div>
          </div>
          <div class="o_bottom">
            <van-button
              size="small"
              plain
              round
              type="info"
              color="#000"
              @click="delOrder(o.id)"
              >删除订单</van-button
            >
            <van-button
              size="small"
              round
              type="info"
              :to="{
                name: 'store',
                params: { storeId: o.shopId, uId: uId, uName: uName },
              }"
              >再来一单</van-button
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Image as VanImage } from "vant";
import { Icon } from "vant";
import { Button } from "vant";
import { Loading } from "vant";
import { Empty } from 'vant';

Vue.use(Empty);
Vue.use(Loading);
Vue.use(Button);
Vue.use(Icon);
Vue.use(VanImage);

export default {
  props: ["uId", "uName"],
  data() {
    return {
      order: [], //订单
    };
  },
  mounted() {
    this.getOrder();
  },
  methods: {
    getOrder() {
      let app = this;
      app.$axios
        .get("/biz/queryOrdersByUserId?userId=" + app.uId)
        .then(function (res) {
          app.order = res.data.reverse();
          for (let i = 0; i < res.data.length; i++) {
            app.order[i].list[0].foodPhoto =
              "http://47.95.13.193/takeOutSystem-1.0-SNAPSHOT/" +
              app.order[i].list[0].foodPhoto;
            app.order[i].roderTime = app.order[i].roderTime.substring(
              0,
              app.order[i].roderTime.length - 2
            );
          }
        });
    },
    delOrder(id) {
      let app = this;
      app.$axios
        .get("/biz/deleteOrderByOid?orderId=" + id)
        .then(function (res) {
          app.getOrder();
        });
    },
  },
};
</script>

<style scoped>
.head {
  text-align: left;
  font-size: 20px;
  margin: 10px;
  font-weight: bold;
}
.order {
  margin-bottom: 60px;
}
.o {
  background: #fff;
  border-radius: 5px;
  margin: 0 2%;
  margin-top: 10px;
  padding: 12px;
  box-sizing: border-box;
  text-align: left;
}
.o_top {
  /* width: 98%; */
  height: 40px;
  /* margin: 1% auto; */
  /* box-sizing: border-box; */
}
.o_img {
  float: left;
}
.o_name {
  float: left;
  width: 190px;
  height: 40px;
  margin-left: 8px;
  /* background: lightblue; */
}
.o_name_n {
  width: 100%;
  height: 20px;
  line-height: 20px;
}
.o_name_n_left {
  float: left;
  width: 80%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.o_name_n_right {
  width: 10%;
  float: right;
}
.o_name_t {
  height: 20px;
  line-height: 20px;
  color: #999999;
  font-size: 12px;
}
.o_songda {
  float: right;
  color: #f36113;
  font-size: 12px;
}
.o_center {
  height: 20px;
}
.o_c_left {
  float: left;
  width: 50%;
  font-size: 13px;
  margin-left: 8px;
}
.o_c_right {
  float: right;
  font-size: 14px;
  font-weight: bold;
}
.o_c_l_left {
  float: left;
  width: 38%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.o_c_r_right {
  float: left;
}
.o_bottom {
  text-align: right;
  margin-top: 15px;
}
</style>


