<template>
  <div>
    <div class="user">
      <div class="u_top">
        <div class="u_img">
          <van-image
            width="60"
            height="60"
            round
            src="https://img01.yzcdn.cn/vant/cat.jpeg"
            ><template v-slot:loading>
              <van-loading type="spinner" size="20" /> </template
          ></van-image>
        </div>
        <div class="u_name">你好，{{ uName }}</div>
        <div class="u_icon">
          <van-icon name="qr" color="#fff" size="18px" />&nbsp;
          <van-icon name="edit" color="#fff" size="18px" />
        </div>
      </div>
      <div class="u_card">
        <div class="u_card_div">
          <div class="u_card_icon"><van-icon size="25px" name="paid" /></div>
          <div class="u_card_text">钱包</div>
        </div>
        <div class="u_card_div">
          <div class="u_card_icon"><van-icon size="25px" name="bill-o" /></div>
          <div class="u_card_text">账单明细</div>
        </div>
        <div class="u_card_div">
          <div class="u_card_icon">
            <van-icon size="25px" name="discount" />
          </div>
          <div class="u_card_text">优惠活动</div>
        </div>
        <div class="u_card_div">
          <div class="u_card_icon">
            <van-icon size="25px" name="cash-back-record" />
          </div>
          <div class="u_card_text">余额退款</div>
        </div>
      </div>
    </div>
    <div class="row">
      <div>
        <div class="r_r">
          <div><van-icon name="setting-o" size="21px" /></div>
          <div>设置</div>
        </div>
        <div class="r_r">
          <div><van-icon name="service-o" size="21px" /></div>
          <div>在线客服</div>
        </div>
        <div class="r_r">
          <div><van-icon name="warning-o" size="21px" /></div>
          <div>隐私政策和用户协议</div>
        </div>
        <router-link class="r_r" tag="div" to="/">
          <div><van-icon name="revoke" size="21px" /></div>
          <div>注销登录</div>
        </router-link>

        <!-- <div class="r_r">
          <div><van-icon name="revoke" size="21px" /></div>
          <div>注销登录</div>
        </div> -->
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Image as VanImage } from "vant";
import { Icon } from "vant";
import { Loading } from "vant";

Vue.use(Loading);
Vue.use(Icon);
Vue.use(VanImage);
export default {
  props: ["uId", "uName"],
  data() {
    return {};
  },
};
</script>

<style scoped>
.user {
  height: 150px;
  background: rgb(91, 188, 226);
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
}
.u_top {
  width: 100%;
  height: 100px;
  line-height: 60px;
  box-sizing: border-box;
  padding: 20px;
}
.u_img {
  float: left;
}
.u_name {
  float: left;
  color: #fff;
  margin-left: 20px;
  font-size: 18px;
}
.u_icon {
  float: right;
  line-height: 70px;
}
.u_card {
  float: left;
  width: 80%;
  height: 60px;
  background: #fff;
  position: relative;
  left: 37px;
  top: 13px;
  border-radius: 10px;
  box-sizing: border-box;
  border: 1px solid #d0d0d0;
}
.u_card_div {
  float: left;
  width: 25%;
  height: 100%;
}
.u_card_div > div {
  width: 100%;
  display: inline-block;
  position: relative;
}
.u_card_icon {
  height: 40px;
  line-height: 60px;
}
.u_card_text {
  font-size: 13px;
  height: 20px;
}
.row {
  margin-top: 50px;
}
.row > div {
  width: 335px;
  margin: 0 20px;
  /* padding-top: 20px; */
  display: inline-block;
  background: #fff;
  border-radius: 10px;
  text-align: left;
  border: 1px solid #e9e9e9;
  box-sizing: border-box;
}
.r_r {
  height: 28px;
  font-size: 15px;
  line-height: 24px;
  margin: 15px 0;
  padding-left: 20px;
}
.r_r > div {
  float: left;
  margin-left: 10px;
}
</style>


