<template>
  <div style="background: #fff">
    <van-nav-bar left-text="返回" left-arrow @click-left="onClickLeft" />
    <van-image
      width="80"
      height="80"
      fit="cover"
      class="s_img"
      :src="shopMessage.photo"
    >
      <template v-slot:loading>
        <van-loading type="spinner" size="20" />
      </template>
    </van-image>
    <!-- 店名 -->
    <div class="s_m_title">
      <van-tag
        size="medium"
        style="font-weight: bold"
        color="#FDEE53"
        text-color="#643915"
        >品牌</van-tag
      ><span class="s_m_t_t">{{ shopMessage.name }} </span>
    </div>
    <!-- 店铺信息 -->
    <div class="s_m_tip">
      {{ shopMessage.score }} <span>|</span> {{ shopMessage.sales
      }}<span>|</span> {{ shopMessage.deliveryTime }}<span>|</span>
      {{ shopMessage.distance }}
    </div>
    <!-- 优惠信息 -->
    <div class="s_m_youhui" v-if="discount != ''" @click="youhui">
      <van-tag type="success" v-if="discount[0].tag == '首单'">首单</van-tag>
      <van-tag type="warning" v-else>特价</van-tag
      ><span class="s_m_y_ge">|</span>
      <div class="s_m_y_m">{{ discount[0].contents }}</div>
      <span class="s_m_y_ge">|</span>
      <div class="s_m_y_num">
        {{ discount.length }}个优惠
        <div class="s_m_y_jiao"></div>
      </div>
    </div>
    <!-- /弹出 -->
    <van-popup
      v-if="discount != ''"
      v-model="show"
      closeable
      position="bottom"
      :style="{ height: '35%', background: '#F5F5F5' }"
      ><div class="yh_tan_title">优惠活动</div>
      <div class="yh_tan_message">
        <div v-for="(dis, index) in discount" :key="index">
          <van-tag type="success" v-if="dis.tag == '首单'">首单</van-tag>
          <van-tag type="warning" v-else>特价</van-tag>
          <span>{{ dis.contents }}</span>
        </div>
      </div></van-popup
    >

    <van-tabs color="#4AA478">
      <!-- 内容 1 -->
      <van-tab title="点餐">
        <!-- 粘性布局 -->
        <van-sticky>
          <!-- 侧边栏 -->
          <van-sidebar
            style="float: left; max-height: 600px; margin-bottom: 50px"
            v-model="activeKey"
            @change="tabSidebar"
          >
            <van-sidebar-item
              v-for="(tab, value) in foodtabs"
              :key="value"
              :title="tab.name"
              style="border-bottom: 1px solid #e9e9ec"
            />
          </van-sidebar>
        </van-sticky>
        <div class="goods">
          <div class="goods_title">{{ selFoodTabs.name }}</div>
          <div class="goods_show" v-for="(s, index) in showFood" :key="index">
            <div style="float: left">
              <van-image width="70" height="70" fit="cover" :src="s.photo">
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template> </van-image
              ><!--  -->
            </div>
            <div style="float: left">
              <div class="f_name">{{ s.name }}</div>
              <div class="f_tag">{{ s.info }}</div>
              <div class="f_sell">月售188份&nbsp;&nbsp;&nbsp;好评率100%</div>
              <div class="f_price">
                <span>￥18</span
                ><span class="addGood" @click="addGood(s.id, s.photo, s.name)"
                  ><svg
                    t="1620526420203"
                    class="icon"
                    viewBox="0 0 1024 1024"
                    version="1.1"
                    xmlns="http://www.w3.org/2000/svg"
                    p-id="2032"
                    width="25"
                    height="25"
                  >
                    <path
                      d="M509.811148 65.080228c-246.678192 0-446.650643 199.972451-446.650643 446.650643s199.972451 446.650643 446.650643 446.650643 446.650643-199.972451 446.650643-446.650643S756.490364 65.080228 509.811148 65.080228zM727.422328 535.8942 533.973454 535.8942l0 193.448874c0 13.353123-10.809183 24.186865-24.162306 24.186865-13.353123 0-24.162306-10.832719-24.162306-24.186865L485.648842 535.8942 292.200992 535.8942c-13.328563 0-24.162306-10.809183-24.162306-24.189935 0-13.328563 10.832719-24.134677 24.162306-24.134677l193.448874 0L485.649865 294.145274c0-13.352099 10.809183-24.162306 24.162306-24.214495 13.352099 0.052189 24.162306 10.861372 24.162306 24.214495l0 193.424314 193.448874 0c13.328563 0 24.162306 10.806113 24.162306 24.134677C751.612263 525.085016 740.77545 535.8942 727.422328 535.8942z"
                      p-id="2033"
                      fill="#4AA478"
                    ></path>
                    <path
                      d="M509.811148 958.893166c-60.365856 0-118.927623-11.82328-174.056198-35.140335-53.247738-22.521946-101.068883-54.764278-142.135184-95.830579-41.066301-41.066301-73.308633-88.887446-95.829556-142.135184-23.318078-55.128575-35.140335-113.689319-35.140335-174.055175s11.82328-118.927623 35.140335-174.056198c22.521946-53.247738 54.764278-101.068883 95.829556-142.135184 41.066301-41.066301 88.887446-73.308633 142.135184-95.829556 55.128575-23.318078 113.690342-35.140335 174.056198-35.140335s118.927623 11.82328 174.056198 35.140335c53.247738 22.521946 101.069907 54.764278 142.135184 95.829556 41.067324 41.066301 73.309656 88.88847 95.830579 142.135184 23.317055 55.129598 35.140335 113.690342 35.140335 174.056198s-11.82328 118.9266-35.140335 174.055175c-22.521946 53.247738-54.764278 101.069907-95.830579 142.135184-41.066301 41.067324-88.887446 73.309656-142.135184 95.830579C628.738771 947.069887 570.177004 958.893166 509.811148 958.893166zM509.811148 65.590857c-246.001787 0-446.13899 200.137203-446.13899 446.140013 0 246.001787 200.137203 446.13899 446.13899 446.13899 246.001787 0 446.140013-200.137203 446.140013-446.13899C955.951161 265.729084 755.813958 65.590857 509.811148 65.590857zM509.811148 754.040568c-13.604856 0-24.673959-11.079336-24.673959-24.698518L485.137189 536.405853 292.199968 536.405853c-13.604856 0-24.673959-11.08036-24.673959-24.700565 0-13.59053 11.06808-24.64633 24.673959-24.64633l192.937221 0 0-192.913685c0-13.580297 11.06808-24.672936 24.671912-24.726148 13.608949 0.053212 24.676006 11.144828 24.676006 24.726148l0 192.913685 192.937221 0c13.604856 0 24.672936 11.0558 24.672936 24.64633 0.014326 6.592139-2.540869 12.792351-7.193843 17.455557-4.663206 4.672416-10.870582 7.246031-17.479093 7.246031L534.485107 536.406876l0 192.937221C534.485107 742.961232 523.417027 754.040568 509.811148 754.040568zM292.199968 488.082264c-13.041014 0-23.650653 10.597359-23.650653 23.623024 0 13.05534 10.609639 23.677259 23.650653 23.677259l193.960527 0 0 193.960527c0 13.054317 10.609639 23.675212 23.650653 23.675212 13.041014 0 23.650653-10.620895 23.650653-23.675212L533.461801 535.382547l193.960527 0c6.334265 0 12.284791-2.466168 16.754592-6.945179 4.459568-4.469801 6.90834-10.411117 6.895037-16.73208 0-13.026688-10.609639-23.624047-23.64963-23.624047L533.461801 488.081241l0-193.936991c0-13.018501-10.610662-23.651676-23.652699-23.702841-13.037944 0.050142-23.648606 10.683317-23.648606 23.702841l0 193.936991L292.199968 488.081241z"
                      p-id="2034"
                      fill="#4AA478"
                    ></path></svg
                ></span>
              </div>
            </div>
          </div>
        </div>
        <div class="car">
          <div class="dingdan">
            <div @click="carPanel">
              <van-badge :content="list.length">
                <svg
                  t="1620535311720"
                  class="icon"
                  viewBox="0 0 1024 1024"
                  version="1.1"
                  xmlns="http://www.w3.org/2000/svg"
                  p-id="1741"
                  width="30"
                  height="30"
                >
                  <path
                    d="M919.3 259.8l-519.2-44.5c-22.5 0-47.6 18.2-40.7 40.7l83.8 312.1c9.5 28.2 31.4 28.5 53.9 28.5h391.7c22.5 0 40.7-18.2 40.7-40.7L960 306c0-22.4-12.5-43.7-40.7-46.2z m-77.9 383.9s69 36.4-3 86.4H349.2L199.2 214l-113-2.5S32 153.3 93 122.4L262.4 136l138.9 507.7h440.1zM449.2 759.3c-39.3 0-71.2 31.9-71.2 71.2 0 39.3 31.9 71.1 71.2 71.1 39.3 0 71.2-31.9 71.2-71.1-0.1-39.4-31.9-71.2-71.2-71.2z m322.1 1.3c-39 0-70.5 31.6-70.5 70.5 0 39 31.6 70.5 70.5 70.5 39 0 70.5-31.6 70.5-70.5 0-39-31.6-70.5-70.5-70.5z"
                    p-id="1742"
                    fill="#ffffff"
                  ></path>
                </svg>
              </van-badge>
            </div>
          </div>
          <div class="car_m">
            <div style="color: white; font-weight: bold">
              ￥{{ this.price }}
            </div>
            <div style="color: #74787d; font-size: 12px; font-weight: bold">
              另需配送费￥{{ shopMessage.transportationPrice }}元
            </div>
          </div>
          <div
            class="car_submit car_submit_a"
            v-if="this.price > this.shopMessage.minPrice"
            @click="carSubmit"
          >
            结算
          </div>
          <div class="car_submit car_submit_b" v-else>
            满￥{{ shopMessage.minPrice }}元起送
          </div>
        </div>
        <van-popup
          v-model="show1"
          position="bottom"
          :style="{ height: '330px' }"
          ><div class="car_tan_title">
            <div class="car_tan_title_left">已选商品</div>
            <div class="car_tan_title_right">
              <svg
                t="1620572350875"
                class="icon"
                viewBox="0 0 1024 1024"
                version="1.1"
                xmlns="http://www.w3.org/2000/svg"
                p-id="6160"
                width="16"
                height="16"
              >
                <path
                  d="M768 224c19.2 0 32 12.8 32 32v128c0 19.2-12.8 32-32 32h-6.4l-54.4 451.2c-3.2 16-16 28.8-32 28.8H348.8c-16 0-28.8-12.8-32-28.8L262.4 416H256c-19.2 0-32-12.8-32-32V256c0-19.2 12.8-32 32-32h512z m-70.4 192H326.4l48 416h268.8l54.4-416zM736 288H288v64h448V288z m-64-160c19.2 0 32 12.8 32 32 0 16-12.8 28.8-28.8 32H352c-19.2 0-32-12.8-32-32 0-16 12.8-28.8 28.8-32H672z"
                  fill="#9A9A9A"
                  p-id="6161"
                ></path>
                <path
                  d="M576 736c19.2 0 32 12.8 32 32 0 16-12.8 28.8-28.8 32H448c-19.2 0-32-12.8-32-32 0-16 12.8-28.8 28.8-32H576z"
                  fill="#9A9A9A"
                  p-id="6162"
                ></path>
              </svg>
              <div
                style="margin-bottom: 2px; display: inline-block"
                @click="clearCar"
              >
                清空
              </div>
            </div>
          </div>
          <div class="car_tan_m">
            <div
              class="car_tan_message"
              v-for="(l, index) in list"
              :key="index"
            >
              <van-image
                width="80"
                height="80"
                round
                fit="cover"
                class="car_tan_message_img"
                :src="l.url"
              >
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template>
              </van-image>
              <div class="car_tan_message_name">{{ l.name }}</div>
              <div class="car_tan_message_price">
                <div class="car_tan_message_price_left">￥18</div>
                <div class="car_tan_message_price_right">
                  <van-stepper
                    v-model="l.buyCount"
                    theme="round"
                    integer
                    @change="addCar"
                  />
                </div>
              </div>
            </div>
          </div>
        </van-popup>
      </van-tab>
      <!-- 内容 2 -->
      <van-tab title="评价">
        <div class="pingjia">
          <div class="pingjia_top">
            <div class="pingjia_top_left">
              <div class="pingjia_top_left_scope">{{ shopMessage.score }}</div>
              <div style="font-size: 13px">综合评分</div>
              <div style="font-size: 13px; color: #94999f">高于周围商家99%</div>
            </div>
            <div class="pingjia_top_right">
              <div style="font-size: 13px">
                <van-rate
                  color="#F19D39"
                  readonly
                  allow-half
                  v-model="shopMessage.serviceScore"
                />
                服务评分<span style="color: #f19d39">{{
                  shopMessage.serviceScore
                }}</span>
              </div>
              <div style="font-size: 13px">
                <van-rate
                  color="#F19D39"
                  readonly
                  allow-half
                  v-model="shopMessage.goodsScore"
                />
                商品评分<span style="color: #f19d39">{{
                  shopMessage.goodsScore
                }}</span>
              </div>
              <div style="font-size: 13px; color: #94999f">
                {{ shopMessage.deliveryTime }}
              </div>
            </div>
          </div>
          <div class="pingjia_bottom">
            <div class="pingjia_bottom_top">
              <van-radio-group v-model="radio" direction="horizontal">
                <van-radio name="0"
                  >全部<span style="font-size: 14px">
                    {{ pingjia.length }}</span
                  ></van-radio
                >
                <van-radio name="1"
                  >满意<span style="font-size: 14px">
                    {{ pingjia1.length }}</span
                  ></van-radio
                >
                <van-radio name="2"
                  >不满意<span style="font-size: 14px">
                    {{ pingjia0.length }}</span
                  ></van-radio
                >
              </van-radio-group>
            </div>
            <div class="pingjia_bottom_bottom">
              <!-- 评价条目 -->
              <div
                class="pingjia_row"
                v-for="(pj, index) in radio == 0
                  ? pingjia
                  : radio == 1
                  ? pingjia1
                  : pingjia0"
                :key="index"
              >
                <div class="p_r_user">
                  <div class="p_r_user_img">
                    <van-image
                      width="35"
                      height="35"
                      round
                      src="https://himg.bdimg.com/sys/portraitn/item/193ce5a4a9e89d8e576a6a66dd9e"
                    >
                      <template v-slot:loading>
                        <van-loading type="spinner" size="20" />
                      </template>
                    </van-image>
                  </div>
                  <div class="p_r_user_mes">
                    <div class="p_r_user_name">{{ pj.account }}</div>
                    <div>
                      <van-rate
                        v-model="pj.score"
                        color="#F19D39"
                        size="15px"
                        readonly
                        allow-half
                      />
                    </div>
                  </div>
                  <div class="p_r_user_time">{{ pj.oederTime }}</div>
                </div>
                <div class="p_r_text">{{ pj.comments }}</div>
              </div>
            </div>
          </div>
        </div>
      </van-tab>
      <!-- 内容 3 -->
      <van-tab title="商家">
        <div class="shangjia">
          <div class="shangjia_first">
            <div class="shangjia_title">配送信息</div>
            <div class="shangjia_first_message">
              <van-tag type="primary">硅谷专送</van-tag> 由商家提供配送，{{
                shopMessage.deliveryTime
              }}送达，{{ shopMessage.distance }} <br />配送费￥{{
                shopMessage.transportationPrice
              }}
            </div>
          </div>
          <div class="shangjia_second">
            <div class="shangjia_title">活动与服务</div>
            <div class="shangjia_second_message" v-if="discount != ''">
              <div v-for="(dis, index) in discount" :key="index">
                <van-tag type="success" v-if="dis.tag == '首单'">首单</van-tag>
                <van-tag type="warning" v-else>特价</van-tag>
                <span>{{ dis.contents }}</span>
              </div>
            </div>
          </div>
        </div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import Vue from "vue";
import { NavBar } from "vant";
import { Image as VanImage } from "vant";
import { Tag } from "vant";
import { Tab, Tabs } from "vant";
import { Sidebar, SidebarItem } from "vant";
import { Sticky } from "vant";
import { Badge } from "vant";
import { Toast } from "vant";
import { Popup } from "vant";
import { Stepper } from "vant";
import { Rate } from "vant";
import { RadioGroup, Radio } from "vant";
import { Loading } from "vant";

Vue.use(Loading);
Vue.use(Radio);
Vue.use(RadioGroup);
Vue.use(Rate);
Vue.use(Stepper);
Vue.use(Popup);
Vue.use(Toast);
Vue.use(Badge);
Vue.use(Sticky);
Vue.use(Sidebar);
Vue.use(SidebarItem);
Vue.use(Tab);
Vue.use(Tabs);
Vue.use(Tag);
Vue.use(VanImage);
Vue.use(NavBar);

export default {
  name: "store",
  props: ["storeId", "uId", "uName"],
  data() {
    return {
      shopMessage: [], //店铺信息
      discount: [], //优惠信息
      show: false, //下方优惠弹出
      activeKey: 0, //侧边栏
      foodtabs: [], //侧边栏信息
      selFoodTabs: [], //当前选中的侧边栏
      showFood: [], // 侧边栏对应商品
      list: [], //购物车
      show1: false, //购物车弹出
      price: 0, //总价格
      pingjia: [], //评价
      pingjia0: [], //差评
      pingjia1: [], //好评
      radio: "0", //单选框
    };
  },
  mounted() {
    let app = this;
    this.getStoreMessage();
    this.getDiscount();
    this.getFoodTab();
    this.getPingjia();
    app.$axios
      .get(
        "biz/queryFoodinfoByShopIdAndFoodCategoryId?shopId=" +
          app.storeId +
          "&foodcategoryId=1"
      )
      .then(function (res) {
        app.showFood = res.data;
        for (let i = 0; i < app.showFood.length; i++) {
          app.showFood[i].photo =
            "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
            app.showFood[i].photo;
        }
      });
  },
  methods: {
    //返回
    onClickLeft() {
      this.$router.push({
        name: "indexstore",
        params: { uId: this.uId, uName: this.uName },
      });
    },
    //获取商家信息
    getStoreMessage() {
      let app = this;
      app.$axios
        .get("/biz/queryInfoByShopId?shopId=" + app.storeId)
        .then(function (res) {
          app.shopMessage = res.data;
          app.shopMessage.photo =
            "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
            app.shopMessage.photo;
          app.shopMessage.deliveryTime =
            "硅谷专送 约" + app.shopMessage.deliveryTime + "分钟";
          app.shopMessage.sales = "月售" + app.shopMessage.sales + "单";
          if (app.shopMessage.distance >= 1000) {
            let a = String(app.shopMessage.distance);
            let b = "距离" + a[0] + "." + a[1] + "km";
            app.shopMessage.distance = b;
          } else {
            app.shopMessage.distance = "距离" + app.shopMessage.distance + "m";
          }
        });
    },
    //获取优惠信息
    getDiscount() {
      let app = this;
      app.$axios
        .get("/biz/querySpecialOfferByShopId?shopId=" + app.storeId)
        .then(function (res) {
          app.discount = res.data;
        });
    },
    // 下方显示优惠
    youhui() {
      this.show = true;
    },
    //食品分类信息
    getFoodTab() {
      let app = this;
      app.$axios
        .get("/biz/queryFoodCategory?id=" + app.storeId)
        .then(function (res) {
          app.foodtabs = res.data;
          app.selFoodTabs = res.data[0];
        });
    },
    //侧边栏切换
    tabSidebar(index) {
      this.selFoodTabs = this.foodtabs[index]; //小标题跟随侧边栏切换
      // 通过商家的食品分类查询对应的食品
      let app = this;
      let id = index + 1;
      app.$axios
        .get(
          "biz/queryFoodinfoByShopIdAndFoodCategoryId?shopId=" +
            app.storeId +
            "&foodcategoryId=" +
            id
        )
        .then(function (res) {
          app.showFood = res.data;
          for (let i = 0; i < app.showFood.length; i++) {
            app.showFood[i].photo =
              "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
              app.showFood[i].photo;
          }
        });
    },
    //添加商品
    addGood(id, url, name) {
      let shopId = this.shopMessage.id;
      let list = this.list;
      if (list.length != 0) {
        for (let i = 0; i < list.length; i++) {
          if (list[i].foodId == id) {
            list[i].buyCount += 1;
            Toast.success("成功添加" + list[i].buyCount + "件");
            break;
          } else if (i == list.length - 1) {
            list.push({
              foodId: id,
              buyCount: 1,
              shopId: shopId,
              url: url,
              name: name,
            });
            Toast.success("添加成功");
            break;
          }
        }
      } else {
        list.push({
          foodId: id,
          buyCount: 1,
          shopId: shopId,
          url: url,
          name: name,
        });
        Toast.success("添加成功");
      }

      let p = 0;
      for (let i = 0; i < list.length; i++) {
        p += list[i].buyCount * 18;
      }
      this.price = p;
    },
    //购物车弹出
    carPanel() {
      this.show1 = true;
    },
    // 购物车添加商品
    addCar() {
      let p = 0;
      for (let i = 0; i < this.list.length; i++) {
        p += this.list[i].buyCount * 18;
      }
      this.price = p;
    },
    //清空购物车
    clearCar() {
      this.list = [];
      this.price = 0;
    },
    //订单提交
    carSubmit() {
      let app = this;
      app.$axios
        .post("/biz/insertOrder", {
          userId: app.uId,
          shopId: app.storeId,
          list: app.list,
        })
        .then(function (res) {
          if (res.data == 1) {
            Toast.success("下单成功");
            app.list = [];
            app.price = 0;
            app.$router.push({
              name: "indexorder",
              params: { uId: app.uId, uName: app.uName },
            });
          } else {
            Toast.fail("失败");
          }
        });
    },
    //获取评价
    getPingjia() {
      let app = this;
      app.$axios
        .get("/biz/queryCommentByShopId?shopId=" + app.storeId)
        .then(function (res) {
          app.pingjia = res.data;
          for (let i = 0; i < res.data.length; i++) {
            app.pingjia[i].account =
              app.pingjia[i].account[0] +
              app.pingjia[i].account[1] +
              app.pingjia[i].account[2] +
              "****";
            app.pingjia[i].score = app.pingjia[i].score * 0.1;
          }
          for (let i = 0; i < res.data.length; i++) {
            if (res.data[i].isGood == 0) {
              app.pingjia0.push(res.data[i]);
            } else {
              app.pingjia1.push(res.data[i]);
            }
          }
        });
    },
  },
};
</script>

