<template>
  <div style="margin-bottom: 50px">
    <!-- 主商家页面 -->
    <van-grid>
      <van-grid-item
        v-for="(p, value) in jiugongge"
        :key="value"
        :text="p.name"
        :icon="p.photo"
        @click="cli_jgg(p.id)"
      />
    </van-grid>
    <div class="fujin" @click="showAllshop">
      <svg
        t="1620384169227"
        class="icon"
        viewBox="0 0 1024 1024"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        p-id="1646"
        width="14"
        height="14"
      >
        <path
          d="M384 320l512 0c38.4 0 64-25.6 64-64 0-38.4-25.6-64-64-64L384 192C345.6 192 320 217.6 320 256 320 294.4 345.6 320 384 320zM896 448 384 448C345.6 448 320 473.6 320 512c0 38.4 25.6 64 64 64l512 0c38.4 0 64-25.6 64-64C960 473.6 934.4 448 896 448zM896 704 384 704c-38.4 0-64 25.6-64 64 0 38.4 25.6 64 64 64l512 0c38.4 0 64-25.6 64-64C960 729.6 934.4 704 896 704zM128 192C89.6 192 64 217.6 64 256c0 38.4 25.6 64 64 64s64-25.6 64-64C192 217.6 166.4 192 128 192zM128 448C89.6 448 64 473.6 64 512c0 38.4 25.6 64 64 64s64-25.6 64-64C192 473.6 166.4 448 128 448zM128 704c-38.4 0-64 25.6-64 64 0 38.4 25.6 64 64 64s64-25.6 64-64C192 729.6 166.4 704 128 704z"
          p-id="1647"
        ></path></svg
      >&nbsp;附<span
        style="border-bottom: 3px solid #5757ff; padding-bottom: 3px"
        >近商</span
      >家
    </div>
    <router-link
      class="goods"
      tag="div"
      v-for="(sj, value) in shangjia"
      :key="value"
      :to="{
        name: 'store',
        params: { storeId: sj.id, uId: uId, uName: uName },
      }"
    >
      <div style="float: left">
        <van-image
          width="100"
          height="100"
          radius="10"
          fit="cover"
          :src="sj.photo"
        >
          <template v-slot:loading>
            <van-loading type="spinner" size="20" /> </template
        ></van-image>
      </div>
      <div class="g_message">
        <van-row>
          <van-col class="business_name" span="24">{{ sj.name }}</van-col>
        </van-row>
        <div>
          <div class="g_m_top">
            <span class="g_m_scope">{{ sj.score }}<span>分</span></span>
            <span class="g_m_sales"><span>月售</span>{{ sj.sales }}</span>
            <span class="g_m_distance">{{ sj.distance }}</span>
            <span class="g_m_time">{{ sj.deliveryTime }}<span>分钟</span></span>
          </div>
        </div>
        <div>
          <div class="g_m_center">
            <span class="g_m_sales"
              ><span>起送￥</span>{{ sj.minPrice }}.0</span
            >
            <span class="g_m_sales"
              ><span>配送￥</span>{{ sj.transportationPrice }}</span
            >
          </div>
        </div>
        <div>
          <div class="g_m_tag">
            <van-tag color="#FEF0E5" text-color="#F46308">
              "{{ sj.notice }}"
            </van-tag>
          </div>
        </div>
      </div>
    </router-link>
  </div>
</template>

<script>
import Vue from "vue";
import { Grid, GridItem } from "vant";
import { Col, Row } from "vant";
import { Image as VanImage } from "vant";
import { Tag } from "vant";
import { Tabbar, TabbarItem } from "vant";
import { Loading } from "vant";

Vue.use(Loading);
Vue.use(Tag);
Vue.use(VanImage);
Vue.use(Col);
Vue.use(Row);
Vue.use(Grid);
Vue.use(GridItem);

export default {
  props: ["uId", "uName"],
  data() {
    return {
      jiugongge: [], //九宫格
      shangjia: [], //商家
    };
  },
  mounted() {
    let app = this;
    // 加载九宫格
    app.$axios.post("/biz/queryBigCategory").then(function (res) {
      app.jiugongge = res.data;
      for (let i = 0; i < res.data.length; i++) {
        app.jiugongge[i].photo =
          "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
          res.data[i].photo;
      }
    });
    this.showAllshop();
  },
  methods: {
    // 九宫格跳转
    cli_jgg(id) {
      let app = this;
      app.$axios
        .get("/biz/queryShopsByBigId?bigcategoryId=" + id)
        .then(function (res) {
          app.shangjia = res.data;
          for (let i = 0; i < res.data.length; i++) {
            app.shangjia[i].photo =
              "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
              res.data[i].photo;
            if (String(res.data[i].distance).length == 4) {
              app.shangjia[i].distance =
                String(res.data[i].distance).slice(0, 1) +
                "." +
                String(res.data[i].distance).slice(1, 2) +
                "km";
            } else {
              app.shangjia[i].distance = res.data[i].distance + "m";
            }
          }
        });
    },
    // 商家全部显示
    showAllshop() {
      let app = this;
      app.$axios.post("/biz/queryAllShopsInfo").then(function (res) {
        app.shangjia = res.data;
        for (let i = 0; i < res.data.length; i++) {
          app.shangjia[i].photo =
            "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
            res.data[i].photo;
          if (String(res.data[i].distance).length == 4) {
            app.shangjia[i].distance =
              String(res.data[i].distance).slice(0, 1) +
              "." +
              String(res.data[i].distance).slice(1, 2) +
              "km";
          } else {
            app.shangjia[i].distance = res.data[i].distance + "m";
          }
        }
      });
    },
  },
};
</script>

<style scoped>
.fujin {
  width: 100%;
  height: 40px;
  line-height: 40px;
  background: rgb(245, 245, 245);
  text-align: left;
  font-size: 14px;
  padding-left: 30px;
  /* margin-top: 50px; */
  box-sizing: border-box;
}
.goods {
  box-sizing: border-box;
  width: 100%;
  height: 120px;
  padding: 10px;
  border-bottom: rgb(229, 229, 230) 1px solid;
  background: #fff;
}
.g_message {
  float: right;
  width: 250px;
  height: 100px;
  box-sizing: border-box;
  padding: 10px;
}
.business_name {
  text-align: left;
  font-weight: bold;
}
.g_m_top {
  text-align: left;
}
.g_m_scope {
  font-weight: bold;
  font-size: 14px;
  color: rgb(255, 115, 0);
}
.g_m_scope > span {
  font-size: 12px;
  color: rgb(255, 115, 0);
}
.g_m_sales {
  font-size: 12px;
  margin-left: 3px;
  color: rgb(73, 73, 73);
}
.g_m_time {
  float: right;
  font-size: 12px;
  margin-left: 3px;
  color: rgb(73, 73, 73);
}
.g_m_distance {
  float: right;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
  margin-left: 6px;
  color: rgb(73, 73, 73);
}
.g_m_center {
  text-align: left;
}
.g_m_tag {
  max-width: 90%;
  text-align: left;
}
.g_m_tag > span {
  color: rgb(255, 165, 91);
  max-width: 85%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
}
</style>


