<template>
  <div>
    <van-search
      @search="onSearch"
      v-model="value"
      placeholder="请输入商家关键词"
    />
    <van-empty image="search" v-if="shangjia == ''" />

    <div v-else style="margin-bottom: 50px">
      <router-link
        class="goods"
        tag="div"
        v-for="(sj, value) in shangjia"
        :key="value"
        :to="{
          name: 'store',
          params: { storeId: sj.id, uId: uId, uName: uName },
        }"
      >
        <div style="float: left">
          <van-image
            width="100"
            height="100"
            radius="10"
            fit="cover"
            :src="sj.photo"
            ><template v-slot:loading>
              <van-loading type="spinner" size="20" /> </template
          ></van-image>
        </div>
        <div class="g_message">
          <van-row>
            <van-col class="business_name" span="24">{{ sj.name }}</van-col>
          </van-row>
          <div>
            <div class="g_m_top">
              <span class="g_m_scope">{{ sj.score }}<span>分</span></span>
              <span class="g_m_sales"><span>月售</span>{{ sj.sales }}</span>
              <span class="g_m_distance">{{ sj.distance }}</span>
              <span class="g_m_time"
                >{{ sj.deliveryTime }}<span>分钟</span></span
              >
            </div>
          </div>
          <div>
            <div class="g_m_center">
              <span class="g_m_sales"
                ><span>起送￥</span>{{ sj.minPrice }}.0</span
              >
              <span class="g_m_sales"
                ><span>配送￥</span>{{ sj.transportationPrice }}</span
              >
            </div>
          </div>
          <div>
            <div class="g_m_tag">
              <van-tag color="#FEF0E5" text-color="#F46308">
                "{{ sj.notice }}"
              </van-tag>
            </div>
          </div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { Notify } from "vant";
import { Search } from "vant";
import { Empty } from "vant";
import { Loading } from "vant";

Vue.use(Loading);
Vue.use(Empty);
Vue.use(Search);
export default {
  props: ["uId", "uName"],
  data() {
    return {
      shangjia: [],
      value: "", //搜索框
    };
  },
  mounted() {},
  methods: {
    // 搜索
    onSearch() {
      this.show2 = false;
      let app = this;
      app.$axios
        .get("/biz/queryAllShopsInfoByName?name=" + app.value)
        .then(function (res) {
          if (res.data != "") {
            app.shangjia = "";
            app.shangjia = res.data;
            for (let i = 0; i < res.data.length; i++) {
              app.shangjia[i].photo =
                "http://47.95.13.193:80/takeOutSystem-1.0-SNAPSHOT/" +
                res.data[i].photo;
              if (String(res.data[i].distance).length == 4) {
                app.shangjia[i].distance =
                  String(res.data[i].distance).slice(0, 1) +
                  "." +
                  String(res.data[i].distance).slice(1, 2) +
                  "km";
              } else {
                app.shangjia[i].distance = res.data[i].distance + "m";
              }
            }
            Notify({
              type: "success",
              duration: "3000",
              message: "查询到" + res.data.length + "条信息",
            });
          } else {
            Notify({
              type: "warning",
              duration: "3000",
              message: "未查询到相关信息",
            });
          }
        });
    },
  },
};
</script>

<style scoped>
.goods {
  box-sizing: border-box;
  width: 100%;
  height: 120px;
  padding: 10px;
  border-bottom: rgb(229, 229, 230) 1px solid;
  background: #fff;
}
.g_message {
  float: right;
  width: 250px;
  height: 100px;
  box-sizing: border-box;
  padding: 10px;
}
.business_name {
  text-align: left;
  font-weight: bold;
}
.g_m_top {
  text-align: left;
}
.g_m_scope {
  font-weight: bold;
  font-size: 14px;
  color: rgb(255, 115, 0);
}
.g_m_scope > span {
  font-size: 12px;
  color: rgb(255, 115, 0);
}
.g_m_sales {
  font-size: 12px;
  margin-left: 3px;
  color: rgb(73, 73, 73);
}
.g_m_time {
  float: right;
  font-size: 12px;
  margin-left: 3px;
  color: rgb(73, 73, 73);
}
.g_m_distance {
  float: right;
  height: 16px;
  line-height: 16px;
  font-size: 12px;
  margin-left: 6px;
  color: rgb(73, 73, 73);
}
.g_m_center {
  text-align: left;
}
.g_m_tag {
  max-width: 90%;
  text-align: left;
}
.g_m_tag > span {
  color: rgb(255, 165, 91);
  max-width: 85%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: left;
}
</style>


